import {
  CFormGroup,
  CInput,
  CLabel
} from '@coreui/react';
import React from 'react';

const FormDate = ({ label, name, placeholder, handleChange, ...otherProps }) => (
  <CFormGroup>
    {label ? (
      <CLabel htmlFor={name}>{label}</CLabel>
    ) : null}
      <CInput type="datetime" id={name} name={name} onChange={handleChange} {...otherProps} />
  </CFormGroup>
);

export default FormDate;
