import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormActions from '../../form-inputs/form-actions.component';
import FormInput from '../../form-inputs/form-input.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import CommunicationTypeEnum from '../../goaml-enums/communication-type-enum/communication-type-enum.component';
import ContactTypeEnum from '../../goaml-enums/contact-type-enum/contact-type-enum.component';

const PhoneTemplate = ({ name, handlePhoneSave, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)
    // let radioItems = ["Yes", "No"];
    const [templatePone, setComponentState] = React.useState({
        "tph_contact_type": '',
        "tph_communication_type": '',
        "tph_country_prefix": '',
        "tph_number": '',
        "tph_extension": '',
        "comments": ''
      });

      const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

      const checkInputs = (e, inputState) => {
          let error = false;
          let val = {};
          for (const [key, value] of Object.entries(inputState)) {
              if (e.target.name === key) {
                  value === '' ? error = true : error = false; 
                  val = key;
              }
          }
          setShowError({
              isEmpty: error,
              value: val
           });
      }

    const handleSaveClick = () => {
        handlePhoneSave(templatePone);
    }

    const handleChange = event => {
        console.log(event.target);
        const { name, value } = event.target;
        setComponentState({ ...templatePone, [name]: value });
    };

    const handleCancelClick = () => {
        console.log("Hello: ", templatePone);
    }
    
    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {name}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="4">
                                    <ContactTypeEnum
                                        label="Contact Type*"
                                        name="tph_contact_type"
                                        handleChange={handleChange}
                                        value={templatePone.tph_contact_type}
                                        onFocus={onFocus}
                                        onBlur={onBlur}
                                        className={`form-control ${!templatePone.tph_contact_type && touched && 'is-invalid'} ${templatePone.tph_contact_type && 'is-valid'}`}
                                    ></ContactTypeEnum>
                                    <FormInput
                                        label="Number*"
                                        name="tph_number" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        value={templatePone.tph_number}
                                        onFocus={onFocus}
                                        onBlur={onBlur}
                                        className={`form-control ${!templatePone.tph_number && touched && 'is-invalid'} ${templatePone.tph_number && 'is-valid'}`} 
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <CommunicationTypeEnum
                                        label="Comm. Type*"
                                        name="tph_communication_type"
                                        handleChange={handleChange}
                                        value={templatePone.tph_communication_type}
                                        onFocus={onFocus}
                                        onBlur={onBlur}
                                        className={`form-control ${!templatePone.tph_communication_type && touched && 'is-invalid'} ${templatePone.tph_communication_type && 'is-valid'}`}
                                    ></CommunicationTypeEnum>
                                    <FormInput
                                        label="Extension"
                                        name="tph_extension"
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        value={templatePone.tph_extension}
                                        onFocus={onFocus}
                                        onBlur={onBlur}
                                        className={`form-control ${!templatePone.tph_extension && touched && 'is-invalid'} ${templatePone.tph_extension && 'is-valid'}`}
                                    />                                    
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Country Prefix*"
                                        name="tph_country_prefix" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        value={templatePone.tph_country_prefix}
                                        onFocus={onFocus}
                                        onBlur={onBlur}
                                        className={`form-control ${!templatePone.tph_country_prefix && touched && 'is-invalid'} ${templatePone.tph_country_prefix && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="12">
                                    <FormTextArea
                                        label="Comments"
                                        name="comments"
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        value={templatePone.comments}
                                        onFocus={onFocus}
                                        onBlur={onBlur}
                                        className={`form-control ${!templatePone.comments && touched && 'is-invalid'} ${templatePone.comments && 'is-valid'}`}
                                    />
                                </CCol>
                            </CFormGroup>
                            <FormActions 
                                saveText="Save Phone"
                                handleSaveClick={handleSaveClick}
                                handleCancelClick={handleCancelClick}
                            ></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default PhoneTemplate;