import ReportTypeActionTypes from './report-type.types'
import { AddPersonToGoAmlReport } from '../goaml-utils/goaml-utils'

export const GetReportType = () => ({
  type: ReportTypeActionTypes.GET_REPORT_TYPE
});

export const SetReportType = reportType => ({
  type: ReportTypeActionTypes.SET_REPORT_TYPE,
  payload: reportType
});

export const AddPersonToReport = (reportType, templatePerson, reportTypeFieldName, reportTypeFieldReference) => {
  return ({
    type: "AWA_RE",
    payload: AddPersonToGoAmlReport(reportType, templatePerson, reportTypeFieldName, reportTypeFieldReference)
  });
};
