import React from 'react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import FormRadioText from '../../form-inputs/form-radio-text.component'
import EntityPersonRoleEnum from '../../goaml-enums/entity-person-role-enum/entity-person-role-enum.component'
import TransactionPerson from '../transaction-person/transaction-person.component'
import FormActions from '../../form-inputs/form-actions.component';

const DirectorTemplate = ({ name, transRef, OnSave, partyType, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    const [entityDirectorInfo, setEntityDirectorInfo] = React.useState({
        "role": 0,
        "t_person": null
    })

    const handleSaveDirectorPerson = (txnRef, partyType, person) => {
        setEntityDirectorInfo({ ...entityDirectorInfo, t_person: person });
    };

    const handleChange = event => {
        const { name, value } = event.target;
        setEntityDirectorInfo({ ...entityDirectorInfo, [name]: value });
    };

    const handleSaveClick = () => {
        let director = {...entityDirectorInfo.t_person, role: entityDirectorInfo.role}
        OnSave(transRef, partyType, director);
    }

    const handleCancelClick = () => {
        console.log("Hello: ", entityDirectorInfo);
    }
    
    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {name}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="4">
                                    <EntityPersonRoleEnum handleChange={handleChange} label="Role" name="role"></EntityPersonRoleEnum>
                                </CCol>
                                <CCol xs="12">
                                    <TransactionPerson OnSave={handleSaveDirectorPerson} transRef={transRef} partyType={partyType} name="Person" label="Person" alias="Person"></TransactionPerson>
                                </CCol>
                            </CFormGroup>
                            <FormActions saveText="Save Director" handleSaveClick={handleSaveClick} handleCancelClick={handleCancelClick}></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default DirectorTemplate;