import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormActions from '../../form-inputs/form-actions.component';
import FormDate from '../../form-inputs/form-date.component';
import FormInput from '../../form-inputs/form-input.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import CountryCodeEnum from '../../goaml-enums/country-code-enum/country-code-enum.component';
import IdentifierTypeEnum from '../../goaml-enums/identifier-type-enum/identifier-type-enum.component';

const IdentificationTemplate = ({ name, handleIdentificationSave, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [templateIdentification, setComponentState] = React.useState({
        "type": 0,
        "number": '',
        "issue_date": '',
        "expiry_date": '',
        "issue_dateSpecified": false,
        "expiry_dateSpecified": false,
        "issued_by": '',
        "issue_country": '',
        "comments": ''
      });

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const handleSaveClick = () => {
        handleIdentificationSave(templateIdentification);
    }

    const handleChange = event => {
        // console.log(event.target);
        const { name, value } = event.target;
        setComponentState({ ...templateIdentification, [name]: value });
    };

    const handleCancelClick = () => {
        console.log("Hello: ", templateIdentification);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {name}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="4">
                                    <IdentifierTypeEnum 
                                        label="Type*" 
                                        name="type" 
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        value={templateIdentification.type} 
                                        className={`form-control ${!templateIdentification.type && touched && 'is-invalid'}${templateIdentification.type && 'is-valid'}`}
                                    ></IdentifierTypeEnum>
                                    <FormInput 
                                        label="Issued by" 
                                        name="issued_by" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        value={templateIdentification.issued_by} 
                                        className={`form-control ${!templateIdentification.issued_by && touched && 'is-invalid'}${templateIdentification.issued_by && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Number*" 
                                        name="number" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        value={templateIdentification.number} 
                                        className={`form-control ${!templateIdentification.number && touched && 'is-invalid'}${templateIdentification.number && 'is-valid'}`}  
                                    />
                                    <CountryCodeEnum 
                                        label="Issue Country*" 
                                        name="issue_country" 
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        value={templateIdentification.issue_country} 
                                        className={`form-control ${!templateIdentification.issue_country && touched && 'is-invalid'}${templateIdentification.issue_country && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormDate 
                                        label="Issue Date" 
                                        name="issue_date" 
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        value={templateIdentification.issue_date} 
                                        className={`form-control ${!templateIdentification.issue_date && touched && 'is-invalid'}${templateIdentification.issue_date && 'is-valid'}`} 
                                    />
                                    <FormDate 
                                        label="Expiry Date" 
                                        name="expiry_date" 
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus} 
                                        value={templateIdentification.expiry_date} 
                                        className={`form-control ${!templateIdentification.expiry_date && touched && 'is-invalid'}${templateIdentification.expiry_date && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="12">
                                    <FormTextArea 
                                        label="Comments" 
                                        name="comments" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        value={templateIdentification.comments} 
                                        className={`form-control ${!templateIdentification.comments && touched && 'is-invalid'}${templateIdentification.comments && 'is-valid'}`}
                                    />
                                </CCol>
                            </CFormGroup>
                            <FormActions 
                                saveText="Save Identification" 
                                handleSaveClick={handleSaveClick} 
                                handleCancelClick={handleCancelClick}
                            ></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default IdentificationTemplate;