import React from 'react';
import TransactionToPartyInfo from '../transaction-party-info/transaction-to-party-info.component'
import TransactionFromPartyInfo from '../transaction-party-info/transaction-from-party-info.component'
import TransactionPerson from '../transaction-person/transaction-person.component'
import TransactionEntity from '../transaction-entity/transaction-entity.components'
import TransactionAccount from '../transaction-account/transaction-account.components'
import TransactionsPartyTypes from '../transaction-party/transaction-party.types'

import {
    CButton,
    CLabel,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow,
    CNav,
    CNavItem,
    CNavLink,
    CTabContent,
    CTabPane,
    CTabs
} from '@coreui/react'
import CIcon from '@coreui/icons-react'

const TransactionParty = ({ name, label, displayPartyType, reportType, transRef, handleSaveParty, ...otherProps }) => {

    const [collapseMulti, setCollapseMulti] = React.useState([false, false, false, false, false, false])
    let [showElements, setShowElements] = React.useState(true)

    const toggleMulti = (type) => {
        let newCollapse = collapseMulti.slice()
        switch (type) {
            case "person-client":
                newCollapse[0] = !collapseMulti[0];
                newCollapse[1] = false;
                newCollapse[2] = false;
                newCollapse[3] = false;
                newCollapse[4] = false;
                newCollapse[5] = false;
                break;
            case "account-client"://
                newCollapse[1] = !collapseMulti[1];
                newCollapse[0] = false;
                newCollapse[2] = false;
                newCollapse[3] = false;
                newCollapse[4] = false;
                newCollapse[5] = false;
                break;
            case "entity-client":
                newCollapse[2] = !collapseMulti[2];
                newCollapse[0] = false;
                newCollapse[1] = false;
                newCollapse[3] = false;
                newCollapse[4] = false;
                newCollapse[5] = false;
                break;
            case "not-person-client":
                newCollapse[3] = !collapseMulti[3];
                newCollapse[4] = false;
                newCollapse[5] = false;
                newCollapse[0] = false;
                newCollapse[1] = false;
                newCollapse[2] = false;
                break;
            case "not-account-client":
                newCollapse[4] = !collapseMulti[4];
                newCollapse[3] = false;
                newCollapse[5] = false;
                newCollapse[0] = false;
                newCollapse[1] = false;
                newCollapse[2] = false;
                break;
            case "not-entity-client":
                newCollapse[5] = !collapseMulti[5];
                newCollapse[3] = false;
                newCollapse[4] = false;
                newCollapse[0] = false;
                newCollapse[1] = false;
                newCollapse[2] = false;
                break;
            default:
        }
        setCollapseMulti(newCollapse)
    }

    return (
        <CRow>
            <CCol xs="12">
                {label ? (
                    <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
                <div className="row">
                    <CCol xs="12">
                        <CTabs activeTab="myClient">
                            <CNav variant="tabs">
                                <CNavItem>
                                    <CNavLink data-tab="myClient">
                                        My Client
                                </CNavLink>
                                </CNavItem>
                                <CNavItem>
                                    <CNavLink data-tab="notMyClient">
                                        Not My Client
                                </CNavLink>
                                </CNavItem>
                            </CNav>
                            <CTabContent>
                                <CTabPane data-tab="myClient">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                                {displayPartyType == TransactionsPartyTypes.FROM ?
                                                    <TransactionFromPartyInfo transRef={transRef} partyType={TransactionsPartyTypes.MY_CLIENT} handleSavePartyInfo={handleSaveParty} name={`Expand and Fill ${label} Overview`} />
                                                    :
                                                    <TransactionToPartyInfo transRef={transRef} partyType={TransactionsPartyTypes.MY_CLIENT} handleSavePartyInfo={handleSaveParty} name={`Expand and Fill ${label} Overview`} />
                                                }
                                                <p>
                                                    <button type="button" onClick={() => { toggleMulti('person-client') }}
                                                        class="btn btn-link">Add Person</button>
                                                    <button type="button" onClick={() => { toggleMulti('account-client') }}
                                                        class="btn btn-link">Add Account</button>
                                                    <button type="button" onClick={() => { toggleMulti('entity-client') }}
                                                        class="btn btn-link">Add Entity</button>
                                                </p>
                                                <CRow>
                                                    <CCol className="col-sm-12">
                                                        <CCollapse show={collapseMulti[0]}>
                                                            <TransactionPerson OnSave={handleSaveParty} partyType={TransactionsPartyTypes.PERSON_CLIENT} transRef={transRef} label="Transaction Person" name="Person" alias="Person"></TransactionPerson>
                                                        </CCollapse>
                                                    </CCol>
                                                    <CCol className="col-sm-12">
                                                        <CCollapse show={collapseMulti[1]}>
                                                            <TransactionAccount OnSave={handleSaveParty} partyType={TransactionsPartyTypes.ACCOUNT_CLIENT} transRef={transRef} name="TransactionAccount" label="Transaction Account"></TransactionAccount>
                                                        </CCollapse>
                                                    </CCol>
                                                    <CCol className="col-sm-12">
                                                        <CCollapse show={collapseMulti[2]}>
                                                            <TransactionEntity OnSave={handleSaveParty} partyType={TransactionsPartyTypes.ENTITY_CLIENT} transRef={transRef} name="TransactionEntity" label="Transaction Entity"></TransactionEntity>
                                                        </CCollapse>
                                                    </CCol>
                                                </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                                <CTabPane data-tab="notMyClient">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                            {displayPartyType == TransactionsPartyTypes.FROM ?
                                                    <TransactionFromPartyInfo transRef={transRef} partyType={TransactionsPartyTypes.NOT_MY_CLIENT} handleSavePartyInfo={handleSaveParty} name={`Expand and Fill ${label} Overview`} />
                                                    :
                                                    <TransactionToPartyInfo transRef={transRef} partyType={TransactionsPartyTypes.NOT_MY_CLIENT} handleSavePartyInfo={handleSaveParty} name={`Expand and Fill ${label} Overview`} />
                                                }
                                                <p>
                                                    <CButton color="primary" onClick={() => { toggleMulti('not-person-client') }}
                                                        class="btn btn-link">Add Person</CButton>{' '}
                                                    <CButton color="primary" onClick={() => { toggleMulti('not-account-client') }}
                                                        class="btn btn-link">Add Account</CButton>{' '}
                                                    <CButton color="primary" onClick={() => { toggleMulti('not-entity-client') }}
                                                        class="btn btn-link">Add Entity</CButton>{' '}
                                                </p>
                                                <CRow>
                                                    <CCol className="col-sm-12">
                                                        <CCollapse show={collapseMulti[3]}>
                                                            <TransactionPerson OnSave={handleSaveParty} partyType={TransactionsPartyTypes.PERSON_NOT_CLIENT} transRef={transRef} label="Transaction Person" name="Person-NC" alias="Person"></TransactionPerson>
                                                        </CCollapse>
                                                    </CCol>
                                                    <CCol className="col-sm-12">
                                                        <CCollapse show={collapseMulti[4]}>
                                                            <TransactionAccount OnSave={handleSaveParty} partyType={TransactionsPartyTypes.ACCOUNT_NOT_CLIENT} transRef={transRef} name="TransactionAccount-NC" label="Transaction Account"></TransactionAccount>
                                                        </CCollapse>
                                                    </CCol>
                                                    <CCol className="col-sm-12">
                                                        <CCollapse show={collapseMulti[5]}>
                                                            <TransactionEntity OnSave={handleSaveParty} partyType={TransactionsPartyTypes.ENTITY_NOT_CLIENT} transRef={transRef} name="TransactionEntity-NC" label="Transaction Entity"></TransactionEntity>
                                                        </CCollapse>
                                                    </CCol>
                                                </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                            </CTabContent>
                        </CTabs>
                    </CCol>
                </div>
            </CCol>
        </CRow>
    );
}

export default TransactionParty;