import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormActions from '../../form-inputs/form-actions.component';
import FormDate from '../../form-inputs/form-date.component';
import FormEmail from '../../form-inputs/form-email.component';
import FormInput from '../../form-inputs/form-input.component';
import FormRadioText from '../../form-inputs/form-radio-text.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import CountryCodeEnum from '../../goaml-enums/country-code-enum/country-code-enum.component';
import EntityLegalFormEnum from '../../goaml-enums/entity-legal-form-enum/entity-legal-form-enum.component';
import ReportAddresses from '../../goaml-sections/report-addresses/report-addresses.component';
import ReportPhones from '../../goaml-sections/report-phones/report-phones.components';
import TransactionDirectors from '../transaction-director/transaction-director.component';


const TransactionEntity = ({ label, name, transRef, partyType, OnSave, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [templateEntity, setComponentState] = React.useState({
        "name": '',
        "commercial_name": '',
        "incorporation_legal_form": '',
        // "incorporation_legal_formSpecified": false,
        "incorporation_number": '',
        "business":'',
        "phones": [],
        "addresses": [],
        "email": '',
        "url": '',
        "incorporation_state": '',
        "incorporation_country_code": '',
        // "incorporation_country_codeSpecified": false,
        "director_id": [],
        "incorporation_date": '',
        // "incorporation_dateSpecified": '',
        "business_closed": false,
        // "business_closedSpecified": null,
        // "date_business_closed": '',
        // "date_business_closedSpecified": false,
        "tax_number": '',
        "tax_reg_number": '',
        "comments": ''
    })

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        for (const [key, value] of Object.entries(inputState)) {
            if (e.target.name === key) {
                value === '' ? error = true : error = false; 
                val = key;
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }
    
    console.log('error', showError)

    const handleChange = event => {
        const { name, value } = event.target;
        setComponentState({ ...templateEntity, [name]: value });
    };

    const handleSaveDirector = (director) => {
        setComponentState({ ...templateEntity, director_id: { ...templateEntity.director_id, director } });
    }

    const handleSaveClick = () => {
        OnSave(transRef, partyType, templateEntity);
    }

    const handleCancelClick = () => {
        console.log("Hello: ", templateEntity);
    }

    const handlePhoneSave = (phone) => {
        templateEntity.phones.push(phone)
    }

    const handleAddressSave = (address) => {
        templateEntity.addresses.push(address)
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {label}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Name*" 
                                                name="name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.name} 
                                                className={`form-control ${!templateEntity.name && touched && 'is-invalid'} ${templateEntity.name && 'is-valid'}`} 
                                            />
                                            <FormInput 
                                                label="Business Area" 
                                                name="business" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.business} 
                                                className={`form-control ${!templateEntity.business && touched && 'is-invalid'} ${templateEntity.business && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Incorporation Province/State" 
                                                name="incorporation_state" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.incorporation_state} 
                                                className={`form-control ${!templateEntity.incorporation_state && touched && 'is-invalid'} ${templateEntity.incorporation_state && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Entity web address" 
                                                name="url" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.url} 
                                                className={`form-control ${!templateEntity.url && touched && 'is-invalid'} ${templateEntity.url && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Registered Name*" 
                                                name="commercial_name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.commercial_name} 
                                                className={`form-control ${!templateEntity.commercial_name && touched && 'is-invalid'} ${templateEntity.commercial_name && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Incorporation Reg Number*" 
                                                name="incorporation_number" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.incorporation_number} 
                                                className={`form-control ${!templateEntity.incorporation_number && touched && 'is-invalid'} ${templateEntity.incorporation_number && 'is-valid'}`}
                                            />
                                            <CountryCodeEnum 
                                                label="incorporation_country_code" 
                                                name="Incorporation Country Code*" 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.incorporation_country_code} 
                                                className={`form-control ${!templateEntity.incorporation_country_code && touched && 'is-invalid'} ${templateEntity.incorporation_country_code && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Reg. Number" 
                                                name="tax_reg_number" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.tax_reg_number} 
                                                className={`form-control ${!templateEntity.tax_reg_number && touched && 'is-invalid'} ${templateEntity.tax_reg_number && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <EntityLegalFormEnum 
                                                label="Incorporation Legal Form*" 
                                                name="incorporation_legal_form" 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.incorporation_legal_form} 
                                                className={`form-control ${!templateEntity.incorporation_legal_form && touched && 'is-invalid'} ${templateEntity.incorporation_legal_form && 'is-valid'}`}
                                            />
                                            <FormDate 
                                                label="Incorporation Date*" 
                                                name="incorporation_date" 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.incorporation_date} 
                                                className={`form-control ${!templateEntity.incorporation_date && touched && 'is-invalid'} ${templateEntity.incorporation_date && 'is-valid'}`}
                                            />
                                            <FormEmail 
                                                label="Email" 
                                                name="email" 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.email} 
                                                className={`form-control ${!templateEntity.email && touched && 'is-invalid'} ${templateEntity.email && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Tax Number" 
                                                name="tax_number" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.tax_number} 
                                                className={`form-control ${!templateEntity.tax_number && touched && 'is-invalid'} ${templateEntity.tax_number && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            <FormRadioText 
                                                label="Business Closed?" 
                                                name="business_closed" 
                                                width="6" items={["Yes", "No"]} 
                                                handleChange={handleChange}
                                                value={templateEntity.business_closed}
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <ReportPhones 
                                                label="Phones*"
                                                name="phones" 
                                                handlePhoneSave={handlePhoneSave}
                                                value={templateEntity.phones}
                                            ></ReportPhones>
                                        </CCol>
                                        <CCol xs="12">
                                            <ReportAddresses 
                                                label="Addresses*"
                                                name="addresses" 
                                                handleAddressSave={handleAddressSave} 
                                                value={templateEntity.addresses}
                                            ></ReportAddresses>
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionDirectors 
                                                label="Directors*" 
                                                name="director_id" 
                                                alias="Director"
                                                partyType={partyType} 
                                                transRef={transRef} 
                                                OnSave={handleSaveDirector}
                                                value={templateEntity.director_id}
                                            ></TransactionDirectors>
                                        </CCol>
                                        <CCol xs="12">
                                            <FormTextArea 
                                                label="Comments" 
                                                name="comments" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, templateEntity)} 
                                                value={templateEntity.comments} 
                                                className={`form-control ${!templateEntity.comments && touched && 'is-invalid'} ${templateEntity.comments && 'is-valid'}`} 
                                            />
                                        </CCol>
                                    </CFormGroup>
                                </CForm>
                                <FormActions 
                                    saveText="Save Entity" 
                                    handleSaveClick={handleSaveClick} 
                                    handleCancelClick={handleCancelClick}
                                ></FormActions>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default TransactionEntity;
