import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class FundsTypeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'A', description:'Deposit'},
                {value: 'B', description:'Electronic Funds Transfer'},
                {value: 'E', description:'Bank Draft'},
                {value: 'F', description:'Managers Cheque'},
                {value: 'G', description:'Travelers cheque'},
                {value: 'H', description:'Life insurance policy'},
                {value: 'K', description:'Cash'},
                {value: 'L', description:'From Account'},
                {value: 'O', description:'Other'},
                {value: 'P', description:'Cheque'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default FundsTypeEnum;