import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class CommunicationTypeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'L', description:'Landline Phone'},
                {value: 'M', description:'Mobile Phone'},
                {value: 'F', description:'Fax'},
                {value: 'S', description:'Satellite Phone'},
                {value: 'P', description:'Pager'},
                {value: 'E', description:'Email'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default CommunicationTypeEnum;