import React from 'react';
import AddressTable from '../../goaml-tables/table-address.component'
import AddressTemplate from '../template-address/template-address.components'
import {
    CLabel,
    CCard,
    CCardBody,
    CCol,
    CFade,
    CRow,
    CNav,
    CNavItem,
    CNavLink,
    CTabContent,
    CTabPane,
    CTabs,
} from '@coreui/react'

const ReportAddresses = ({ name, label, handleAddressSave, ...otherProps }) => {

    let [showElements, setShowElements] = React.useState(true)
    const tab1Title = `Add ${name}`;
    const tab2Title = `${name} Table`;

    return (
        <CRow>
            <CCol xs="12">
                {label ? (
                    <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
                <div className="row">
                    <CCol xs="12">
                        <CTabs activeTab="Tab1">
                            <CNav variant="tabs">
                                <CNavItem>
                                    <CNavLink data-tab="Tab1">
                                        {tab1Title}
                                    </CNavLink>
                                </CNavItem>
                                <CNavItem>
                                    <CNavLink data-tab="Tab2">
                                        {tab2Title}
                                    </CNavLink>
                                </CNavItem>
                            </CNav>
                            <CTabContent>
                                <CTabPane data-tab="Tab1">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                                <CRow>
                                                    <CCol className="col-sm-12">
                                                        <AddressTemplate name="Add an Address" handleAddressSave={handleAddressSave}></AddressTemplate>
                                                    </CCol>
                                                </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                                <CTabPane data-tab="Tab2">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                                <CRow>
                                                    <CCol className="col-sm-12">
                                                        <AddressTable name={name} />
                                                    </CCol>
                                                </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                            </CTabContent>
                        </CTabs>
                    </CCol>
                </div>
            </CCol>
        </CRow>
    );
}

export default ReportAddresses;