import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class IdentifierTypeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'A', description:'Drivers license'},
                {value: 'B', description:'National Identity Card'},
                {value: 'C', description:'Passport'},
                {value: 'E', description:'Voters Registration Card'},
                {value: 'R', description:'RC No'},
                {value: 'O', description:'3rd Party ID'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default IdentifierTypeEnum;