import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class TransactionItemStatusEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'A', description:'Bought'},
                {value: 'B', description:'Sold'},
                {value: 'C', description:'Rent'},
                {value: 'D', description:'Leased'},
                {value: 'E', description:'Mortgaged'},
                {value: 'F', description:'Donated'},
                {value: 'G', description:'Destroyed'},
                {value: 'H', description:'Other'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default TransactionItemStatusEnum;