import React from 'react';
import CombinedTable from '../../goaml-tables/combined-table.component'

const fields = ['Id','Address', 'City', 'State', 'Postal', 'Country']

class ReportLocation extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            usersData : [
            {
                Id: 1,
                Address: "Building 21",
                City: "Victoria Island",
                State: "Lagos",
                Postal: "01104",
                Country: "Nigeria"
            }]
        }
    }

    render(){
        const { usersData } = this.state;
        const { name } = this.props;
        
        return(
            <CombinedTable name="Reporting Location" fields={fields} tableData={usersData} />
        )
    }
}

export default ReportLocation;