import React from 'react';
import FormReactSelect from '../../form-inputs/form-react-select.component'

class ReportCodeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            reportIndicators : [
                {value: 'CTR', label:'Cash Transaction Report'},
                {value: 'STR', label:'Suspicious Transaction Report'},
                {value: 'EFT', label:'Electronic Funds Transfer'},
                {value: 'IFT', label:'International Funds Transfer'},
                {value: 'TFR', label:'Terror Financing Report'},
                {value: 'BCR', label:'Border Cash Report'},
                {value: 'UTR', label:'Unusual Transaction Report'},
                {value: 'AIF', label:'Additional Information File'},
                {value: 'IRI', label:'Incoming Request for Information  International'},
                {value: 'ORI', label:'Outgoing Request for Information  International'},
                {value: 'IRD', label:'Incoming Request for Information  Domestic'},
                {value: 'ORD', label:'Outgoing Request for Information  Domestic'}
            ]
        }
    }

    render(){
        const { reportIndicators } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormReactSelect label={label} name={name} handleChange={handleChange} options={reportIndicators} />
        )

    }
}

export default ReportCodeEnum;