import React from 'react';
import ReportCodeEnum from '../../goaml-enums/report-code-enum/report-code-enum.component';
import FormActions from '../../form-inputs/form-actions.component';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import { connect } from 'react-redux';
import { SetReportType } from '../../../redux/report-type/report-type.actions';
import { selectReportType } from '../../../redux/report-type/report-type.selectors';


const ReportIndicator = ({ name, reportType, setReportType, ...otherProps }) => {

    let [collapsed, setCollapsed] = React.useState(true)
    let [showElements, setShowElements] = React.useState(true)

    const handleReactSelectTyeChange = event => {
        let type = event.map((item) => {
            return item.value
        })
        reportType.report_indicators = type;
    };
    
    const handleSaveClick = () => {
        setReportType(reportType);
    }

    const handleCancelClick = () => {
        console.log("Hello: ", reportType);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                        {name}
                        <div className="card-header-actions">
                            <CButton 
                            color="link" 
                            className="card-header-action btn-minimize" 
                            onClick={() => setCollapsed(!collapsed)}
                            >
                            <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                            </CButton>
                        </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="12">
                                    <ReportCodeEnum name="report_indicators" handleChange={handleReactSelectTyeChange} />
                                </CCol>
                            </CFormGroup>
                            <FormActions handleSaveClick={handleSaveClick} handleCancelClick={handleCancelClick}></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    reportType: selectReportType(state)
});

const mapDispatchToProps = dispatch => ({
    setReportType: reportType => dispatch(SetReportType(reportType))
});

export default connect(mapStateToProps, mapDispatchToProps)(ReportIndicator);
