import React from 'react';
import DirectorTable from '../../goaml-tables/table-director.component'
import DirectorTemplate from '../template-director/template-director.components'
import {
    CLabel,
    CCard,
    CCardBody,
    CCol,
    CFade,
    CRow,
    CNav,
    CNavItem,
    CNavLink,
    CTabContent,
    CTabPane,
    CTabs,
  } from '@coreui/react'

const TransactionDirectors = ({ name, label, transRef, partyType, OnSave, ...otherProps }) => {
    
    let [showElements, setShowElements] = React.useState(true)
    const tab1Title = `Add ${label}`;
    const tab2Title = `${label} Table`;

    return (
        <CRow>
            <CCol xs="12">
                {label ? (
                    <CLabel htmlFor={label}>{label}</CLabel>
                ) : null}
                <div className="row">
                    <CCol xs="12">
                        <CTabs activeTab="Tab1">
                            <CNav variant="tabs">
                                <CNavItem>
                                <CNavLink data-tab="Tab1">
                                    {tab1Title}
                                </CNavLink>
                                </CNavItem>
                                <CNavItem>
                                <CNavLink data-tab="Tab2">
                                    {tab2Title}
                                </CNavLink>
                                </CNavItem>
                            </CNav>
                            <CTabContent>
                                <CTabPane data-tab="Tab1">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                            <CRow>
                                                <CCol className="col-sm-12">
                                                    <DirectorTemplate name="director" label="Add a Director" transRef={transRef} OnSave={OnSave} partyType={partyType} ></DirectorTemplate>
                                                </CCol>
                                            </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                                <CTabPane data-tab="Tab2">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                            <CRow>
                                                <CCol className="col-sm-12">
                                                    <DirectorTable name={name} />
                                                </CCol>
                                            </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                            </CTabContent>
                        </CTabs>
                    </CCol>
                </div>
            </CCol>
        </CRow>
    );
}

export default TransactionDirectors;
