import React from 'react';
import './form-radio-text.style.css'
import {
    CInput,
    CCol,
    CLabel
  } from '@coreui/react'

const FormRadioText = ({ label, name, placeholder, width, items, handleChange }) => (
    <div className="form-group">
                {label ? (
                    <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
                <div className="row">
                    {items ? 
                    items.map((item) => {
                        return(
                            <CCol xs={width}>
                                <div className="input-group">
                                    <span className="input-group-addon">
                                        <input type="radio" name={name} />
                                    </span>
                                    <CInput name={name} onChange={handleChange} defaultValue={item} disabled />
                                </div>
                            </CCol>
                        )
                    }) : null}
                </div>
            </div>
  );

export default FormRadioText;