import React from 'react';
import CombinedTable from '../../goaml-tables/combined-table.component'

const fields = ['Id','FirstName', 'LastName', 'Date of Birth', 'IdNumber', 'Passport', 'status']

class ReportPerson extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            usersData : [
                {
                Id: 1,
                FirstName: "John",
                LastName: "Doe",
                "Date of Birth": "18/03/1992",
                IdNumber: "A25529928",
                Passport: "",
                status: 'Active'
            }]
        }
    }

    render(){
        const { usersData } = this.state;
        const { name } = this.props;
        
        return(
            <CombinedTable name="Reporting Person" closed={"true"} fields={fields} tableData={usersData} />
        )
    }
}

export default ReportPerson;