import React from 'react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import FormRadioText from '../../form-inputs/form-radio-text.component'
import AccountPersonRoleEnum from '../../goaml-enums/account-person-role-enum/account-person-role-enum.component'
import TransactionPerson from '../transaction-person/transaction-person.component'
import FormActions from '../../form-inputs/form-actions.component';

const SignatoryTemplate = ({ name, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    const [componentState, setComponentState] = React.useState();

    const handleSaveClick = () => {
        //handlePhoneSave(componentState);
    }

    const handleChange = event => {
        const { name, value } = event.target;
        setComponentState({ ...componentState, [name]: value });
    };

    const handleCancelClick = () => {
        console.log("Hello: ", componentState);
    }
    
    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {name}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="8">
                                    <FormRadioText label="Is Primary?" name="IsPrimary" width="6" items={["Yes", "No"]} />
                                </CCol>
                                <CCol xs="4">
                                    <AccountPersonRoleEnum label="Role" name="Role"></AccountPersonRoleEnum>
                                </CCol>
                                <CCol xs="12">
                                    <TransactionPerson name="Person" label="Person" alias="Person"></TransactionPerson>
                                </CCol>
                            </CFormGroup>
                            <FormActions saveText="Save Signatory" handleSaveClick={handleSaveClick} handleCancelClick={handleCancelClick}></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default SignatoryTemplate;