import React from 'react'
import { CButton } from '@coreui/react'

const FormActions = ({saveText, handleCancelClick, handleSaveClick}) => {

    return (
        <div className="form-actions">
            <CButton color="primary" onClick={handleSaveClick}>{ !saveText ? "Save changes": saveText }</CButton>
            <CButton color="secondary" className="btn-cancel-form-actions" onClick={handleCancelClick}>Cancel</CButton>
        </div>
    )
}

export default FormActions;