import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormActions from '../../form-inputs/form-actions.component';
import FormDate from '../../form-inputs/form-date.component';
import FormEmail from '../../form-inputs/form-email.component';
import FormGenderSelect from '../../form-inputs/form-gender-select.component';
import FormInput from '../../form-inputs/form-input.component';
import FormRadioText from '../../form-inputs/form-radio-text.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import CountryCodeEnum from '../../goaml-enums/country-code-enum/country-code-enum.component';
import ReportAddresses from '../../goaml-sections/report-addresses/report-addresses.component';
import ReportIdentifications from "../../goaml-sections/report-identifications/report-identifications.component";
import ReportPhones from '../../goaml-sections/report-phones/report-phones.components';


const PersonTemplate = ({ label, name, alias,  transRef, partyType, OnSave, handleSaveInfoClick, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    // let radioItems = ["Yes", "No"];
    const [templatePerson, setComponentState] = React.useState({
        "gender": '',
        "title": '',
        "first_name": '',
        "middle_name": '',
        "prefix": '',
        "last_name": '',
        "birthdate": '',
        "birth_place": '',
        "mothers_name": '',
        "alias": '',
        "ssn": '',
        "passport_number": '',
        "passport_country": '',
        "id_number": '',
        "nationality1": '',
        "nationality2": '',
        "nationality3": '',
        "residence": '',
        "phones": [],
        "addresses": [],
        "email": '',
        "occupation": '',
        "employer_name": '',
        "employer_address_id": '',
        "employer_phone_id": '',
        "identification": [],
        "date_deceased":'',
        "tax_number": '',
        "tax_reg_number": '',
        "source_of_wealth": '',
        "comments": ''
    })
    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        for (const [key, value] of Object.entries(inputState)) {
            if (e.target.name === key) {
                value === '' ? error = true : error = false; 
                val = key;
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }

    const handleChange = event => {
        console.log(event.target);
        const { name, value } = event.target;
        setComponentState({ ...templatePerson, [name]: value });
    };

    const handleSaveClick = () => {
        console.log(templatePerson);
        OnSave(transRef, partyType, templatePerson);
    }
    
    const handleCancelClick = () => {
        console.log("Hello: ", templatePerson);
    }

    const handlePhoneSave = (phone) => {
        templatePerson.phones.push(phone)
    }

    const handleAddressSave = (address) => {
        templatePerson.addresses.push(address)
    }

    const handleIdentificationSave = (identification) => {
        templatePerson.identification.push(identification)
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>      {label}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="First Name*" 
                                                name="first_name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.first_name}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.first_name && touched && 'is-invalid'} ${templatePerson.first_name && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Title*" 
                                                name="title" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.title}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.title && touched && 'is-invalid'} ${templatePerson.title && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Alias/Known As" 
                                                name="alias" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.first_name}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.first_name && touched && 'is-invalid'} ${templatePerson.first_name && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Mother's Name" 
                                                name="mothers_name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.mothers_name}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.mothers_name && touched && 'is-invalid'} ${templatePerson.mothers_name && 'is-valid'}`}
                                            />
                                            <CountryCodeEnum 
                                                label="Nationality 1" 
                                                name="nationality1" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.nationality1}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.nationality1 && touched && 'is-invalid'} ${templatePerson.nationality1 && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Passport" 
                                                name="passport_number" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.passport_number}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.passport_number && touched && 'is-invalid'} ${templatePerson.passport_number && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="Source of Wealth" 
                                                name="source_of_wealth" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.source_of_wealth}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.source_of_wealth && touched && 'is-invalid'} ${templatePerson.source_of_wealth && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Middle Name" 
                                                name="middle_name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.middle_name}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.middle_name && touched && 'is-invalid'} ${templatePerson.middle_name && 'is-valid'}`}
                                            />
                                            <FormGenderSelect 
                                                label="Gender" 
                                                name="gender" 
                                                handleChange={handleChange}
                                                value={templatePerson.gender}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.gender && touched && 'is-invalid'} ${templatePerson.gender && 'is-valid'}`}  
                                            />
                                            <FormDate 
                                                label="Birth Date" 
                                                name="birthdate" 
                                                handleChange={handleChange}
                                                value={templatePerson.birthdate}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.birthdate && touched && 'is-invalid'} ${templatePerson.birthdate && 'is-valid'}`}  
                                            />
                                            <CountryCodeEnum 
                                                label="Country of Residence" 
                                                name="residence"
                                                handleChange={handleChange}
                                                value={templatePerson.residence}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.residence && touched && 'is-invalid'} ${templatePerson.residence && 'is-valid'}`}
                                            />
                                            <CountryCodeEnum 
                                                label="Nationality 2" 
                                                name="nationality2" 
                                                handleChange={handleChange}
                                                value={templatePerson.nationality2}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.nationality2 && touched && 'is-invalid'} ${templatePerson.nationality2 && 'is-valid'}`}  
                                            />
                                            <FormInput 
                                                label="Occupation" 
                                                name="occupation" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.occupation}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.occupation && touched && 'is-invalid'} ${templatePerson.occupation && 'is-valid'}`} 
                                            />
                                            <FormInput 
                                                label="Employer Name" 
                                                name="employer_name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.employer_name}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.employer_name && touched && 'is-invalid'} ${templatePerson.employer_name && 'is-valid'}`} 
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="SurName/Last Name" 
                                                name="last_name" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.last_name}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.last_name && touched && 'is-invalid'} ${templatePerson.last_name && 'is-valid'}`} 
                                            />
                                            <FormInput 
                                                label="Prefix" 
                                                name="prefix" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.prefix}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.prefix && touched && 'is-invalid'} ${templatePerson.prefix && 'is-valid'}`}  
                                                />
                                            <FormInput 
                                                label="Birth Place" 
                                                name="birth_place" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.birth_place}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.birth_place && touched && 'is-invalid'} ${templatePerson.birth_place && 'is-valid'}`} 
                                            />
                                            <FormEmail 
                                                label="Email address" 
                                                name="email" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.email}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.email && touched && 'is-invalid'} ${templatePerson.email && 'is-valid'}`}  
                                            />
                                            <FormInput 
                                                label="Social Security Number" 
                                                name="ssn" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.ssn}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.ssn && touched && 'is-invalid'} ${templatePerson.ssn && 'is-valid'}`} 
                                            />
                                            <FormInput 
                                                label="Tax Number" 
                                                name="tax_number" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.tax_number}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.tax_number && touched && 'is-invalid'} ${templatePerson.tax_number && 'is-valid'}`}  
                                            />
                                            <FormInput 
                                                label="Tax Reg. Number" 
                                                name="tax_reg_number" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.tax_reg_number}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, templatePerson)} 
                                                className={`from-control ${!templatePerson.tax_reg_number && touched && 'is-invalid'} ${templatePerson.tax_reg_number && 'is-valid'}`} 
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <ReportPhones 
                                                label="Phone" 
                                                name="phones" 
                                                handlePhoneSave={handlePhoneSave}
                                                value={templatePerson.phones}
                                            >
                                            </ReportPhones>
                                        </CCol>
                                        <CCol xs="12">
                                            <ReportAddresses 
                                                label="Address" 
                                                name="addresses" 
                                                handleAddressSave={handleAddressSave}
                                                value={templatePerson.addresses}
                                            >
                                            </ReportAddresses>
                                        </CCol>
                                        <CCol xs="12">
                                            <ReportIdentifications 
                                                label="Identification" 
                                                name="identification" 
                                                handleIdentificationSave={handleIdentificationSave}
                                                value={templatePerson.identification}
                                            >
                                            </ReportIdentifications>
                                        </CCol>
                                        <CCol xs="12">
                                            <CRow>
                                                <CCol xs="6">
                                                    <FormRadioText
                                                        label="Deceased?" 
                                                        name="deceased" 
                                                        width="6" items={["Yes", "No"]} 
                                                        handleChange={handleChange}
                                                    />
                                                </CCol>
                                                <CCol xs="6">
                                                    <FormDate 
                                                        label="Date Deceased" 
                                                        name="date_deceased" 
                                                        handleChange={handleChange}
                                                        value={templatePerson.date_deceased} 
                                                    />
                                                </CCol>
                                            </CRow>
                                        </CCol>
                                        <CCol xs="12">
                                            <FormTextArea 
                                                label="Comments" 
                                                name="comments" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={templatePerson.comments} 
                                            />
                                        </CCol>
                                    </CFormGroup>
                                    <FormActions 
                                        saveText={`Save ${alias}`} 
                                        handleSaveClick={handleSaveClick} 
                                        handleCancelClick={handleCancelClick}>
                                    </FormActions>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default PersonTemplate;