import React from 'react';
import IdentificationTable from '../../goaml-tables/table-identity.component'
import IdentificationTemplate from '../template-identification/template-identification.components'
import {
    CLabel,
    CCard,
    CCardBody,
    CCol,
    CFade,
    CRow,
    CNav,
    CNavItem,
    CNavLink,
    CTabContent,
    CTabPane,
    CTabs,
  } from '@coreui/react'

const ReportIdentifications = ({ name, label, handleIdentificationSave, ...otherProps }) => {
    
    let [showElements, setShowElements] = React.useState(true)
    const tab1Title = `Add ${name}`;
    const tab2Title = `${name} Table`;

    return (
        <CRow>
            <CCol xs="12">
                {label ? (
                    <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
                <div className="row">
                    <CCol xs="12">
                        <CTabs activeTab="Tab1">
                            <CNav variant="tabs">
                                <CNavItem>
                                <CNavLink data-tab="Tab1">
                                    {tab1Title}
                                </CNavLink>
                                </CNavItem>
                                <CNavItem>
                                <CNavLink data-tab="Tab2">
                                    {tab2Title}
                                </CNavLink>
                                </CNavItem>
                            </CNav>
                            <CTabContent>
                                <CTabPane data-tab="Tab1">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                            <CRow>
                                                <CCol className="col-sm-12">
                                                    <IdentificationTemplate name="Add Identification Document" handleIdentificationSave={handleIdentificationSave}></IdentificationTemplate>
                                                </CCol>
                                            </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                                <CTabPane data-tab="Tab2">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                            <CRow>
                                                <CCol className="col-sm-12">
                                                    <IdentificationTable name={name} />
                                                </CCol>
                                            </CRow>
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                            </CTabContent>
                        </CTabs>
                    </CCol>
                </div>
            </CCol>
        </CRow>
    );
}

export default ReportIdentifications;