import React from 'react';
import {
    CFormGroup,
    CTextarea,
    CLabel  } from '@coreui/react'

const FormTextArea = ({ label, name, rows, placeholder, handleChange }) => (
    <CFormGroup>
        {label ? (
            <CLabel htmlFor={name}>{label}</CLabel>
        ) : null}
        <CTextarea 
            name={name} 
            id={name} 
            rows={rows}
            placeholder={placeholder}
            onChange={handleChange}
        />
    </CFormGroup>
  );

export default FormTextArea;
