import React from 'react';
import {
    CFormGroup,
    CLabel,
    CSelect
  } from '@coreui/react'
  
class FormGenderSelect extends React.Component {
    constructor() {
      super();
    }
  
    render() {
      const { label, name, handleChange, ...otherProps } = this.props;
      const options = [
        {value:"0", description: "--select--"},
        {value:"M", description: "Male"},
        {value:"F", description: "Female"}
      ];

      return (
        <CFormGroup>
            {label ? (
                <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
            {options && options.length > 0 && (
                <CSelect custom name={name} id={name} onChange={handleChange}>
                    {options.map((option, index) => {
                        return <option value={option.value}>{option.description}</option>;
                    })}
                </CSelect>
            )}
        </CFormGroup>
      );
    }
}

export default FormGenderSelect;