import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormActions from '../../form-inputs/form-actions.component';
import FormInput from '../../form-inputs/form-input.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import ContactTypeEnum from '../../goaml-enums/contact-type-enum/contact-type-enum.component';
import CountryCodeEnum from '../../goaml-enums/country-code-enum/country-code-enum.component';

const TemplateAddress = ({ name, handleAddressSave, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [templateAddress, setComponentState] = React.useState({
        "address_type": '',
        "address": '',
        "town": '',
        "city": null,
        "zip": '',
        "country_code": '',
        "state": '',
        "comments": ''
      });

      const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        for (const [key, value] of Object.entries(inputState)) {
            if (e.target.name === key) {
                value === '' ? error = true : error = false; 
                val = key;
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }
    
    console.log('error', showError)

    const handleSaveClick = () => {
        handleAddressSave(templateAddress);
    }

    const handleChange = event => {
        const { name, value } = event.target;
        setComponentState({ ...templateAddress, [name]: value });
    };

    const handleCancelClick = () => {
        console.log("Hello: ", templateAddress);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {name}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="4">
                                    <ContactTypeEnum 
                                        label="Type*" 
                                        name="address_type"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.address_type} 
                                        className={`form-control ${!templateAddress.address_type && touched && 'is-invalid'} ${templateAddress.address_type && 'is-valid'}`}
                                    ></ContactTypeEnum>
                                    <FormInput 
                                        label="City*" 
                                        name="city" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.city} 
                                        className={`form-control ${!templateAddress.city && touched && 'is-invalid'} ${templateAddress.city && 'is-valid'}`}
                                    />
                                    <CountryCodeEnum 
                                        label="Nationality1"
                                        name="country_code"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.country_code} 
                                        className={`form-control ${!templateAddress.country_code && touched && 'is-invalid'} ${templateAddress.country_code && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Address*" 
                                        name="address" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.address} 
                                        className={`form-control ${!templateAddress.address && touched && 'is-invalid'} ${templateAddress.address && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Province/State" 
                                        name="state" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.state} 
                                        className={`form-control ${!templateAddress.state && touched && 'is-invalid'} ${templateAddress.state && 'is-valid'}`}
                                    />                                    
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Town" 
                                        name="town" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        value={templateAddress.town} 
                                        className={`form-control ${!templateAddress.town && touched && 'is-invalid'} ${templateAddress.town && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Postal/Zip" 
                                        name="zip" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.zip} 
                                        className={`form-control ${!templateAddress.zip && touched && 'is-invalid'} ${templateAddress.zip && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="12">
                                    <FormTextArea 
                                        label="Comments" 
                                        name="comments" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange}
                                        value={templateAddress.comments} 
                                        className={`form-control ${!templateAddress.comments && touched && 'is-invalid'} ${templateAddress.comments && 'is-valid'}`} 
                                    />
                                </CCol>
                            </CFormGroup>
                            <FormActions 
                                saveText="Save Address" 
                                handleSaveClick={handleSaveClick} 
                                handleCancelClick={handleCancelClick}
                            ></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default TemplateAddress;