import React from 'react';
import {
    CFormGroup,
    CInput,
    CLabel  } from '@coreui/react'

const FormAmount = ({ label, name, placeholder, handleChange, ...otherProps }) => (
  <CFormGroup>
    {label ? (
      <CLabel htmlFor={name}>{label}</CLabel>
    ) : null}
      <CInput id={name} name={name} onChange={handleChange} {...otherProps} placeholder={placeholder} />
  </CFormGroup>
);

export default FormAmount;
