import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import { connect } from 'react-redux';
import FormAmount from 'src/components/form-inputs/form-amount.component';
import { SetReportType } from '../../../redux/report-type/report-type.actions';
import { selectReportType } from '../../../redux/report-type/report-type.selectors';
import FormActions from '../../form-inputs/form-actions.component';
import FormInput from '../../form-inputs/form-input.component';
import CountryCodeEnum from '../../goaml-enums/country-code-enum/country-code-enum.component';
import CurrenciesEnum from '../../goaml-enums/currencies-enum/currencies-enum.component';
import FundsTypeEnum from '../../goaml-enums/funds-type-enum/funds-type-enum.component';
import PartyTypeEnum from '../../goaml-enums/party-type-enum/party-type-enum.components';
import TransactionPerson from '../transaction-person/transaction-person.component';


const TransactionFromPartyInfo =({ name, transRef, partyType, handleSavePartyInfo, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(true)
    const [touched, setTouched] = React.useState(false);
    // let radioItems = ["Yes", "No"];
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [collapseMulti, setCollapseMulti] = React.useState([false, false, false, false, false, false])
    let [showElements, setShowElements] = React.useState(true)
    const [transpartyInfo, setTranspartyInfo] = React.useState({
        "partyType": '',
        "partyInfo": {
            "from_funds_code": '',
            "from_funds_comment": '',
            "from_foreign_currency": {
                "foreign_currency_code": '',
                "foreign_amount": '',
                "foreign_exchange_rate": '',
            },
            "from_country": ''
        }
    })

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        for (const [key, value] of Object.entries(inputState)) {
            if (e.target.name === key) {
                value === '' ? error = true : error = false; 
                val = key;
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }
    
    console.log('error', showError)

    const toggleMulti = (type) => {
        let newCollapse = collapseMulti.slice()
        switch (type) {
            case "person-conductor":
                newCollapse[0] = !collapseMulti[0];
                break;
            default:
        }
        setCollapseMulti(newCollapse)
    }

    const handleChange = event => {
        // console.log(event.target);
        const { name, value } = event.target;
        setTranspartyInfo({ ...transpartyInfo, [name]: value });
    };

    const handleSaveClick = () => {
        console.log('great', fromPtyInfo)
       const fromPtyInfo = handleSavePartyInfo(transRef, partyType, transpartyInfo);
        if (fromPtyInfo == "partyType")
            setTranspartyInfo({ ...transpartyInfo, fromPtyInfo });
        else if (fromPtyInfo == "foreign_currency_code" || fromPtyInfo == "foreign_amount" || fromPtyInfo == "foreign_exchange_rate")
            setTranspartyInfo({
                ...transpartyInfo, partyInfo: { ...transpartyInfo.partyInfo, 
                    from_foreign_currency: { ...transpartyInfo.partyInfo.from_foreign_currency, fromPtyInfo }}
            });
        else
            setTranspartyInfo({ ...transpartyInfo, partyInfo: { ...transpartyInfo.partyInfo, fromPtyInfo } });
    }

    const handleCancelClick = () => {
        // console.log("Hello: ", transpartyInfo);
    }

    const handleSaveConductor = (conductor) => {
        setTranspartyInfo({ ...transpartyInfo, partyInfo: { ...transpartyInfo.partyInfo, t_conductor: conductor } });
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {name}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="6">
                                            <FundsTypeEnum 
                                                label="Funds Code*" 
                                                name="from_funds_code"
                                                handleChange={handleChange} 
                                                value={transpartyInfo.from_funds_code}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)} 
                                                className={`from-control ${!transpartyInfo.from_funds_code && touched && 'is-invalid'} ${transpartyInfo.from_funds_code && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            <FormInput 
                                                label="Funds Comment" 
                                                name="from_funds_comment" 
                                                placeholder="Enter ..." 
                                                handleChange={handleChange}
                                                value={transpartyInfo.from_funds_comment}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                className={`from-control ${!transpartyInfo.from_funds_comment && touched && 'is-invalid'} ${transpartyInfo.from_funds_comment && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            <CountryCodeEnum 
                                                handleChange={handleChange} 
                                                label="Country*" 
                                                name="from_country"
                                                value={transpartyInfo.from_country}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.from_country && touched && 'is-invalid'} ${transpartyInfo.from_country && touched && 'is-valid'}`} 
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            <PartyTypeEnum 
                                                handleChange={handleChange} 
                                                label="Related Party Type*" 
                                                name="partyType"
                                                value={transpartyInfo.partyType}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.partyType && touched && 'is-invalid'} ${transpartyInfo.partyType && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <CurrenciesEnum 
                                                handleChange={handleChange} 
                                                label="Currency Code*" 
                                                name="foreign_currency_code"
                                                value={transpartyInfo.foreign_currency_code}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.foreign_currency_code && touched && 'is-invalid'} ${transpartyInfo.foreign_currency_code && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormAmount 
                                                handleChange={handleChange} 
                                                label="Exchange Rate*" 
                                                name="foreign_exchange_rate"
                                                value={transpartyInfo.foreign_exchange_rate}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.foreign_exchange_rate && touched && 'is-invalid'} ${transpartyInfo.foreign_exchange_rate && 'is-valid'}`} 
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormAmount 
                                                handleChange={handleChange} 
                                                label="Amount*" 
                                                name="foreign_amount"
                                                value={transpartyInfo.foreign_amount}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.foreign_amount && touched && 'is-invalid'} ${transpartyInfo.foreign_amount && 'is-valid'} `}
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <p>
                                                <button type="button" onClick={() => { toggleMulti('person-conductor') }}
                                                    class="btn btn-link">Add Conductor</button>
                                            </p>
                                            <row>
                                                <CCollapse show={collapseMulti[0]}>
                                                    <TransactionPerson OnSave={handleSaveConductor} transRef={transRef} partyType={partyType} label="Add a Conductor" name="conductor" alias="Conductor" ></TransactionPerson>
                                                </CCollapse>
                                            </row>
                                        </CCol>
                                    </CFormGroup>
                                </CForm>
                                <FormActions 
                                    saveText="Save Party Overview" 
                                    handleSaveClick={handleSaveClick} 
                                    handleCancelClick={handleCancelClick}>
                                    </FormActions>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    partyType: selectReportType(state)
});

const mapDispatchToProps = dispatch => ({
    setReportType: partyType => dispatch(SetReportType(partyType))
});

export default connect(mapStateToProps, mapDispatchToProps)(TransactionFromPartyInfo);
