import React from 'react';
import {
    CFormGroup,
    CInput,
    CInputGroup,
    CInputGroupAppend,
    CInputGroupText,
    CLabel  } from '@coreui/react'
import CIcon from '@coreui/icons-react'

const FormEmail = ({ label, name, placeholder, handleChange, ...otherProps }) => (
  <CFormGroup>
          {label ? (
            <CLabel htmlFor={name}>{label}</CLabel>
          ) : null}
            <CInputGroup>
                <CInput type="email" id={name} name={name} onChange={handleChange} {...otherProps} placeholder={placeholder} />
                <CInputGroupAppend>
                <CInputGroupText>
                    <CIcon name="cil-envelope-closed" />
                </CInputGroupText>
                </CInputGroupAppend>
            </CInputGroup>
        </CFormGroup>
);

export default FormEmail;
