export const downloadObjectAsXml = (exportObj, exportName) => {
    //This will not work without limits, though. You can only download about 1MB of data. 
    //For example, var storageObj = []; for (var i=0; i<1000000; ++i) storageObj.push('aaa'); 
    //gives "download Failed - Network error" in Chrome 61
    
    var dataStr = "data:text/plain;charset=utf-8," + encodeURIComponent(exportObj);
    var downloadAnchorNode = document.createElement('a');
    downloadAnchorNode.setAttribute("href", dataStr);
    downloadAnchorNode.setAttribute("download", exportName + ".xml");
    document.body.appendChild(downloadAnchorNode); // required for firefox
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
}

export const downloadXmlAsFile = (xmltext, filename) => {
    // This doesn't work in mac firefox and opens the tab with content instead of downloading in mac safari
    var pom = document.createElement('a');

    var filename = `${filename}.xml`;
    var pom = document.createElement('a');
    var bb = new Blob([xmltext], { type: 'text/plain' });

    pom.setAttribute('href', window.URL.createObjectURL(bb));
    pom.setAttribute('download', filename);

    pom.dataset.downloadurl = ['text/plain', pom.download, pom.href].join(':');
    pom.draggable = true;
    pom.classList.add('dragout');

    pom.click();
}