import ReportPersonTypes from '../report-person/report-person.types';


export const RetrieveTransactionFromReport = (fullReport, internal_ref_number) => {
    const existingTxn = fullReport.transaction.find(
        reportTransaction => reportTransaction.internal_ref_number === internal_ref_number
    );
    return existingTxn;
}


export const AddTransactionToGoAmlReport = (reportType, templateTxn) => {
    if(templateTxn == null)
        return;
    if(templateTxn.internal_ref_number == null)
        return;

    const existingTxn = reportType.Items.find(
        reportTransaction => reportTransaction.internal_ref_number === templateTxn.internal_ref_number
    );
    
    if (existingTxn) {
        const reportTxns = reportType.Items.map(reportTransaction =>
            reportTransaction.internal_ref_number === templateTxn.internal_ref_number
                ? { ...reportTransaction, ...templateTxn }
                : reportTransaction
        );
        return reportTxns;
    }
}; 

