import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import { connect } from 'react-redux';
import { RetrieveTransactionFromReport } from '../../../redux/goaml-utils/goaml-utils';
import { SetReportType } from '../../../redux/report-type/report-type.actions';
import { selectReportType } from '../../../redux/report-type/report-type.selectors';
import FormActions from '../../form-inputs/form-actions.component';
import TransactionInfo from '../transaction-info/transaction-info.components';
import TransactionItem from '../transaction-item/transaction-item.components';
import TransactionParty from '../transaction-party/transaction-party.component';
import TransactionsPartyTypes from '../transaction-party/transaction-party.types';


const TransactionTemplate = ({ name, reportType, setReportType, ...otherProps }) => {

    let [collapsed, setCollapsed] = React.useState(false)
    let [showElements, setShowElements] = React.useState(true)
    let [transitState, setTransitState] = React.useState({
        transRef: null,
        fromMyClientParty: { "Item": null, "Info": null },
        fromNotMyClientParty: { "Item": null, "Info": null },
        toMyClientParty: { "Item": null, "Info": null },
        toNotMyClientParty: { "Item": null, "Info": null }
    })

    const handleTransInfoSaveClick = (reportTxns, transaction) => {
        setTransitState({ ...transitState, transRef: transaction.internal_ref_number })
        setReportType({ ...reportType, transaction: reportTxns });
    }

    const handleSaveFromParty = (txnRef, partyType, txnParty) => {
        if (transitState.transRef == null) {
            alert("Please save Transaction Overview First");
            return;
        }
        let filteredParties = [];
        const reportTxn = RetrieveTransactionFromReport(reportType, txnRef);
        switch (partyType) {
            case TransactionsPartyTypes.MY_CLIENT:
                setTransitState({ ...transitState, fromMyClientParty: { ...transitState.fromMyClientParty, Info: txnParty } })
                filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_from_my_client"));
                filteredParties.push({ "t_from_my_client": { ...txnParty.partyInfo, ...transitState.fromMyClientParty.Item } });
                reportTxn.Items = filteredParties;
                break;
            case TransactionsPartyTypes.NOT_MY_CLIENT:
                setTransitState({ ...transitState, fromNotMyClientParty: { ...transitState.fromNotMyClientParty, Info: txnParty } })
                filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_from"));
                filteredParties.push({ "t_from": { ...txnParty.partyInfo, ...transitState.fromNotMyClientParty.Item } });
                reportTxn.Items = filteredParties;
                break;
            case TransactionsPartyTypes.PERSON_CLIENT:
                if (transitState.fromMyClientParty.Info == null) {
                    alert("Please save From Party Overview First");
                    return;
                }
                if (transitState.fromMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.fromMyClientParty.Info.partyType == "Person") {
                        const fromPerson = { "from_person": txnParty };
                        setTransitState({ ...transitState, fromMyClientParty: { ...transitState.fromMyClientParty, Item: fromPerson } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_from_my_client"));
                        filteredParties.push({ "t_from_my_client": { ...transitState.fromMyClientParty.Info.partyInfo, ...fromPerson } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save From Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.ENTITY_CLIENT:
                if (transitState.fromMyClientParty.Info == null) {
                    alert("Please save From Party Overview First");
                    return;
                }
                if (transitState.fromMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.fromMyClientParty.Info.partyType == "Entity") {
                        const fromEntity = { "from_entity": txnParty };
                        setTransitState({ ...transitState, fromMyClientParty: { ...transitState.fromMyClientParty, Item: fromEntity } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_from_my_client"));
                        filteredParties.push({ "t_from_my_client": { ...transitState.fromMyClientParty.Info.partyInfo, ...fromEntity } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save From Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.PERSON_NOT_CLIENT:
                if (transitState.fromMyClientParty.Info == null) {
                    alert("Please save From Party Overview First");
                    return;
                }
                if (transitState.fromNotMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.fromNotMyClientParty.Info.partyType == "Person") {
                        const fromPerson = { "from_person": txnParty };
                        setTransitState({ ...transitState, fromNotMyClientParty: { ...transitState.fromNotMyClientParty, Item: fromPerson } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_from"));
                        filteredParties.push({ "t_from": { ...transitState.fromNotMyClientParty.Info.partyInfo, ...fromPerson } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save From Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.ENTITY_NOT_CLIENT:
                if (transitState.fromMyClientParty.Info == null) {
                    alert("Please save From Party Overview First");
                    return;
                }
                if (transitState.fromNotMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.fromNotMyClientParty.Info.partyType == "Entity") {
                        const from_entity = { "from_entity": txnParty };
                        setTransitState({ ...transitState, fromNotMyClientParty: { ...transitState.fromNotMyClientParty, Item: from_entity } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_from"));
                        filteredParties.push({ "t_from": { ...transitState.fromNotMyClientParty.Info.partyInfo, ...from_entity } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save From Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.ACCOUNT_CLIENT:

                break;
            case TransactionsPartyTypes.ACCOUNT_NOT_CLIENT:

                break;
            default:
                break;
        }

        let reportTxns = reportType.Items.map(reportTransaction =>
            reportTransaction.internal_ref_number === txnRef
                ? reportTxn
                : reportTransaction)
        setReportType({ ...reportType, transaction: reportTxns });
    }

    const handleSaveToParty = (txnRef, partyType, txnParty) => {
        if (transitState.transRef == null) {
            alert("Please save Transaction Overview First");
            return;
        }
        let filteredParties = [];
        const reportTxn = RetrieveTransactionFromReport(reportType, txnRef);
        switch (partyType) {
            case TransactionsPartyTypes.MY_CLIENT:
                setTransitState({ ...transitState, toMyClientParty: { ...transitState.toMyClientParty, Info: txnParty } })
                filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_to_my_client"));
                filteredParties.push({ "t_to_my_client": { ...txnParty.partyInfo, ...transitState.toMyClientParty.Item } });
                reportTxn.Items = filteredParties;
                break;
            case TransactionsPartyTypes.NOT_MY_CLIENT:
                setTransitState({ ...transitState, toNotMyClientParty: { ...transitState.toNotMyClientParty, Info: txnParty } })
                filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_to"));
                filteredParties.push({ "t_to": { ...txnParty.partyInfo, ...transitState.toNotMyClientParty.Item } });
                reportTxn.Items = filteredParties;
                break;

            case TransactionsPartyTypes.PERSON_CLIENT:
                if (transitState.toMyClientParty.Info == null) {
                    alert("Please save To Party Overview First");
                    return;
                }
                if (transitState.toMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.toMyClientParty.Info.partyType == "Person") {
                        const toPerson = { "to_person": txnParty };
                        setTransitState({ ...transitState, toMyClientParty: { ...transitState.toMyClientParty, Item: toPerson } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_to_my_client"));
                        filteredParties.push({ "t_to_my_client": { ...transitState.toMyClientParty.Info.partyInfo, ...toPerson } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save To Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.ENTITY_CLIENT:
                if (transitState.toMyClientParty.Info == null) {
                    alert("Please save To Party Overview First");
                    return;
                }
                if (transitState.toMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.toMyClientParty.Info.partyType == "Entity") {
                        const toEntity = { "to_entity": txnParty };
                        setTransitState({ ...transitState, toMyClientParty: { ...transitState.toMyClientParty, Item: toEntity } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_to_my_client"));
                        filteredParties.push({ "t_to_my_client": { ...transitState.toMyClientParty.Info.partyInfo, ...toEntity } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save To Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.PERSON_NOT_CLIENT:
                if (transitState.toNotMyClientParty.Info == null) {
                    alert("Please save To Party Overview First");
                    return;
                }
                if (transitState.toNotMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.toNotMyClientParty.Info.partyType == "Person") {
                        const toPerson = { "to_person": txnParty };
                        setTransitState({ ...transitState, toNotMyClientParty: { ...transitState.toNotMyClientParty, Item: toPerson } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_to"));
                        filteredParties.push({ "t_to": { ...transitState.toNotMyClientParty.Info.partyInfo, ...toPerson } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save To Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.ENTITY_NOT_CLIENT:
                if (transitState.toNotMyClientParty.Info == null) {
                    alert("Please save To Party Overview First");
                    return;
                }
                if (transitState.toNotMyClientParty.Info.hasOwnProperty("partyType")) {
                    if (transitState.toNotMyClientParty.Info.partyType == "Entity") {
                        const toEntity = { "to_entity": txnParty };
                        setTransitState({ ...transitState, toNotMyClientParty: { ...transitState.toNotMyClientParty, Item: toEntity } })
                        filteredParties = reportTxn.Items.filter(item => !item.hasOwnProperty("t_to"));
                        filteredParties.push({ "t_to": { ...transitState.toNotMyClientParty.Info.partyInfo, ...toEntity } });
                        reportTxn.Items = filteredParties;
                    }
                }
                else {
                    alert("Please save To Party Overview First");
                    break;
                }
                break;
            case TransactionsPartyTypes.ACCOUNT_CLIENT:

                break;
            case TransactionsPartyTypes.ACCOUNT_NOT_CLIENT:

                break;
            default:
                break;
        }

        let reportTxns = reportType.Items.map(reportTransaction =>
            reportTransaction.internal_ref_number === txnRef
                ? reportTxn
                : reportTransaction)
        setReportType({ ...reportType, transaction: reportTxns });
    }

    const handleTransItemSaveClick = (reportTxns, transaction) => {
        if (transitState.transRef == null) {
            alert("Please save From Transaction Overview First");
            return;
        }
    }

    const handleSaveClick = () => {
        if (transitState.transRef == null) {
            alert("Please save From Transaction Overview First");
            return;
        }
    }

    const handleCancelClick = () => {
        console.log("Hello: ", reportType);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {name}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="12">
                                            <TransactionInfo handleSaveInfoClick={handleTransInfoSaveClick} transRef={transitState.transRef} label="Transaction Overview" name="TransactionOverview" />
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionParty handleSaveParty={handleSaveFromParty} transRef={transitState.transRef} label="From Party" name="FromParty" displayPartyType={TransactionsPartyTypes.FROM} />
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionParty handleSaveParty={handleSaveToParty} transRef={transitState.transRef} label="To Party" name="ToParty" displayPartyType={TransactionsPartyTypes.TO} />
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionItem handleSaveItem={handleTransItemSaveClick} transRef={transitState.transRef} label="Transaction Item" name="goods_services"></TransactionItem>
                                        </CCol>
                                    </CFormGroup>
                                    <FormActions saveText="Submit Transaction" handleSaveClick={handleSaveClick} handleCancelClick={handleCancelClick}></FormActions>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    reportType: selectReportType(state)
});

const mapDispatchToProps = dispatch => ({
    setReportType: reportType => dispatch(SetReportType(reportType))
});

export default connect(mapStateToProps, mapDispatchToProps)(TransactionTemplate);