import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormAmount from '../../form-inputs/form-amount.component';
import FormDate from '../../form-inputs/form-date.component';
import FormInput from '../../form-inputs/form-input.component';
import FormRadioText from '../../form-inputs/form-radio-text.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import AccountTypeEnum from '../../goaml-enums/account-type-enum/account-type-enum.component';
import CurrenciesEnum from '../../goaml-enums/currencies-enum/currencies-enum.component';
import TransactionEntity from '../transaction-entity/transaction-entity.components';
import TransactionSignatories from '../transaction-signatory/transaction-signatory.component';


const TransactionAccount = ({ name, label, transRef, partyType, OnSave, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [stateAccount, setStateAccount] = React.useState({
        "institution_name": '',
        "institution_code": '',
        "swift": '',
        "non_banking_institution": false,
        "branch": '',
        "account": '',
        "currency_code": '',
        "account_name": '',
        "iban": '',
        "client_number": '',
        "personal_account_type": '',
        "t_entity": '',
        "signatory": '',
        "is_primary": '',
        "t_person": '',
        "role": '',
        "opened": '',
        "closed": '',
        "balance": '',
        "date_balance": '',
        "status_code": '',
        "beneficiary": '',
        "beneficiary_comment": '',
        "comments": '',
    })

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        for (const [key, value] of Object.entries(inputState)) {
            if (e.target.name === key) {
                value === '' ? error = true : error = false; 
                val = key;
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }
    
    console.log('error', showError)

    const handleChange = event => {
        const { name, value } = event.target;
        setStateAccount({ ...stateAccount, [name]: value });
    };

    const handleSaveClick = () => {
        OnSave(transRef, partyType, stateAccount);
    }

    const handleCancelClick = () => {
        console.log("Hello: ", stateAccount);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                        {label}
                        <div className="card-header-actions">
                            <CButton 
                            color="link" 
                            className="card-header-action btn-minimize" 
                            onClick={() => setCollapsed(!collapsed)}
                            >
                            <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                            </CButton>
                        </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Account*" 
                                        name="account" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.account} 
                                        className={`form-control ${!stateAccount.account && touched && 'is-invalid'} ${stateAccount.account && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Iban" 
                                        name="iban" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.iban} 
                                        className={`form-control ${!stateAccount.iban && touched && 'is-invalid'} ${stateAccount.iban && 'is-valid'}`}
                                    />
                                    <FormRadioText 
                                        label="Non Banking Institution?" 
                                        name="non_banking_institution" 
                                        width="6" 
                                        items={["Yes", "No"]}
                                        value={stateAccount.non_banking_institution}
                                        handleChange={handleChange}
                                    />
                                    <CurrenciesEnum 
                                        label="Currency Code*" 
                                        name="currency_code"
                                        value={stateAccount.currency_code}
                                        handleChange={handleChange} 
                                    />
                                    <FormDate 
                                        label="Opened*" 
                                        name="Opened"
                                        value={stateAccount.opened}
                                        handleChange={handleChange} 
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Account name" 
                                        name="account_name" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.account_name} 
                                        className={`form-control ${!stateAccount.account_name && touched && 'is-invalid'} ${stateAccount.account_name && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Institution Name*" 
                                        name="institution_name" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.institution_name} 
                                        className={`form-control ${!stateAccount.institution_name && touched && 'is-invalid'} ${stateAccount.institution_name && 'is-valid'}`}
                                    />
                                    <FormRadioText 
                                        label="Code or Swift?" 
                                        name="swift" 
                                        width="6" 
                                        items={["Institution Code", "Swift"]}
                                        handleChange={handleChange}
                                        value={stateAccount.swift} 
                                    />
                                    <FormAmount 
                                        label="Balance*" 
                                        name="balance" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.balance} 
                                        className={`form-control ${!stateAccount.balance && touched && 'is-invalid'} ${stateAccount.balance && 'is-valid'}`}
                                    />
                                    <FormDate 
                                        label="Closed" 
                                        name="Closed"
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.closed} 
                                        className={`form-control ${!stateAccount.closedt && touched && 'is-invalid'} ${stateAccount.closed && 'is-valid'}`}
                                    />                    
                                </CCol>
                                <CCol xs="4">
                                    <AccountTypeEnum 
                                        label="Account Type*" 
                                        name="personal_account-type" 
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.personal_account_type} 
                                        className={`form-control ${!stateAccount.personal_account_type && touched && 'is-invalid'} ${stateAccount.personal_account_type && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Branch Code*" 
                                        name="branch" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.branch} 
                                        className={`form-control ${!stateAccount.branch && touched && 'is-invalid'} ${stateAccount.branch && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Role*" 
                                        name="role" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.role} 
                                        className={`form-control ${!stateAccount.role && touched && 'is-invalid'} ${stateAccount.role && 'is-valid'}`}
                                    />
                                    <FormDate 
                                        label="Date of Balance*" 
                                        name="date_balance"
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.date_balance} 
                                        className={`form-control ${!stateAccount.date_balance && touched && 'is-invalid'} ${stateAccount.date_balance && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Client Number" 
                                        name="client_number" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.client_number} 
                                        className={`form-control ${!stateAccount.client_number && touched && 'is-invalid'} ${stateAccount.client_number && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Beneficiary" 
                                        name="beneficiary" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.beneficiary} 
                                        className={`form-control ${!stateAccount.beneficiary && touched && 'is-invalid'} ${stateAccount.beneficiary && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="8">
                                    <FormTextArea 
                                        label="Beneficiary Comment" 
                                        name="beneficiary_comment" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        onKeyUp={(e) => checkInputs(e, stateAccount)} 
                                        value={stateAccount.beneficiary_comment} 
                                        className={`form-control ${!stateAccount.beneficiary_comment && touched && 'is-invalid'} ${stateAccount.beneficiary_comment && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="12">
                                    <TransactionEntity 
                                        label="Transaction Entity"
                                        name="transaction_entity"
                                        handleChange={handleChange}
                                        value={stateAccount.transaction_entity}
                                    ></TransactionEntity>
                                </CCol>
                                <CCol xs="12">
                                    <TransactionSignatories 
                                        name="Signatories" 
                                        label="signatory"
                                        handleChange={handleChange}
                                        value={stateAccount.signatory}
                                    ></TransactionSignatories>
                                </CCol>
                                <CCol xs="12">
                                    <FormTextArea 
                                        label="Comments" 
                                        name="comments" 
                                        placeholder="Enter ..."
                                        handleChange={handleChange}
                                        value={stateAccount.comments}
                                    />
                                </CCol>
                            </CFormGroup>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default TransactionAccount;