import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import { connect } from 'react-redux';
import { RetrieveTransactionFromReport } from '../../../redux/goaml-utils/goaml-utils';
import { SetReportType } from '../../../redux/report-type/report-type.actions';
import { selectReportType } from '../../../redux/report-type/report-type.selectors';
import FormActions from '../../form-inputs/form-actions.component';
import FormAmount from '../../form-inputs/form-amount.component';
import FormDate from '../../form-inputs/form-date.component';
import FormInput from '../../form-inputs/form-input.component';
import FormRadioText from '../../form-inputs/form-radio-text.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import ConductionTypeEnum from '../../goaml-enums/conduction-type-enum/conduction-type-enum.component';

const TransactionInfo = ({ label, name, reportType, setReportType, handleSaveInfoClick, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [transaction, setTransactionState] = React.useState({
        "transactionnumber": '',
        "internal_ref_number": '',
        "transaction_location": '',
        "transaction_description": '',
        "date_transaction": '',
        "teller": '',
        "authorized": '',
        "late_deposit": '',
        "date_posting": '',
        // "date_postingSpecified": false,
        //"value_dateSpecified": false,
        "transmode_code": '',
        "transmode_comment": '',
        "amount_local": '',
        "Items": [],
        "goods_services": '',
        "comments": ''
    })

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        for (const [key, value] of Object.entries(inputState)) {
            if (e.target.name === key) {
                value === '' ? error = true : error = false; 
                val = key;
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }
    
    console.log('error', showError)

    const handleChange = event => {
        const { name, value } = event.target;
        setTransactionState({ ...transaction, [name]: value });
    };

    const handleSaveClick = () => {
        const existingTxn = RetrieveTransactionFromReport(reportType, transaction);
        if (existingTxn) {
            setTransactionState({ ...transaction, ...existingTxn });
            let reportTxns = reportType.transaction.map(reportTransaction =>
                reportTransaction.internal_ref_number === transaction.internal_ref_number
                    ? transaction
                    : reportTransaction
            );
            handleSaveInfoClick(reportTxns, transaction);
        }
        else {
            let txn = reportType.transaction;
            txn.push(transaction)
            handleSaveInfoClick(txn, transaction);
        }
    }

    const handleCancelClick = () => {
        console.log("Hello: ", reportType);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {label}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Number*" 
                                                placeholder="Enter ..." 
                                                name="transactionnumber"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)} 
                                                handleChange={handleChange} 
                                                value={transaction.transactionnumber} 
                                                className={`form-control ${!transaction.transactionnumber && touched && 'is-invalid'} ${transaction.transactionnumber && 'is-valid'}`}
                                            />
                                            <FormInput 
                                                label="TransMode Comment" 
                                                name="transmode_comment" 
                                                placeholder="Enter ..." 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                value={transaction.transmode_comment} 
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.transmode_comment && touched && 'is-invalid'} ${transaction.transmode_comment && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Internal Reference Number" 
                                                placeholder="Enter ..." 
                                                name="internal_ref_number" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                value={transaction.internal_ref_number} 
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.internal_ref_number && touched && 'is-invalid'} ${transaction.internal_ref_number && 'is-valid'}`}
                                            />
                                            <FormAmount 
                                                label="Local Amount*" 
                                                name="amount_local" 
                                                placeholder="0.00" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                value={transaction.amount_local} 
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.amount_local && touched && 'is-invalid'} ${transaction.amount_local && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <ConductionTypeEnum 
                                                label="TransMode Code*" 
                                                name="transmode_code" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                handleChange={handleChange} 
                                                value={transaction.transmode_code} 
                                                className={`form-control ${!transaction.transmode_code && touched && 'is-invalid'} ${transaction.transmode_code && 'is-valid'}`}
                                            />
                                            <FormDate 
                                                label="Trans Date*" 
                                                name="date_transaction" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                value={transaction.date_transaction} 
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.date_transaction && touched && 'is-invalid'} ${transaction.date_transaction && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormDate 
                                                label="Post Date*" 
                                                name="date_posting" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)} 
                                                value={transaction.date_posting} 
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.date_posting && touched && 'is-invalid'} ${transaction.date_posting && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Teller" 
                                                placeholder="Enter ..." 
                                                name="teller" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                value={transaction.teller} 
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.teller && touched && 'is-invalid'} ${transaction.teller && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Authorised" 
                                                name="authorized" 
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus} 
                                                value={transaction.authorized} 
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.authorized && touched && 'is-invalid'} ${transaction.authorized && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <CRow>
                                                <CCol xs="6">
                                                    <FormInput 
                                                        label="Location*" 
                                                        name="transaction_location" 
                                                        placeholder="Enter ..."
                                                        onBlur={onBlur}
                                                        onFocus={onFocus} 
                                                        onKeyUp={(e) => checkInputs(e, transaction)}
                                                        value={transaction.transaction_location} 
                                                        handleChange={handleChange} 
                                                        className={`form-control ${!transaction.transaction_location && touched && 'is-invalid'} ${transaction.transaction_location && 'is-valid'}`}
                                                        />
                                                </CCol>
                                                <CCol xs="6">
                                                    <FormRadioText 
                                                        label="Late Deposit?" 
                                                        name="late_deposit" 
                                                        onBlur={onBlur}
                                                        onFocus={onFocus}
                                                        width="6" items={["Yes", "No"]} 
                                                        onKeyUp={(e) => checkInputs(e, transaction)}
                                                        value={transaction.late_deposit} 
                                                        handleChange={handleChange} 
                                                        className={`form-control ${!transaction.late_deposit && touched && 'is-invalid'} ${transaction.late_deposit && 'is-valid'}`}
                                                        />
                                                </CCol>
                                            </CRow>
                                        </CCol>
                                        <CCol xs="12">
                                            <CRow>
                                                <CCol xs="6">
                                                    <FormRadioText 
                                                        label="From Party?" 
                                                        items={["My Client", "Not My Client"]} 
                                                        name="txn_from" 
                                                        width="6" 
                                                        onBlur={onBlur}
                                                        onFocus={onFocus}
                                                        onKeyUp={(e) => checkInputs(e, transaction)}
                                                        value={radioItems} 
                                                        className={`form-control ${!transaction.txn_from && touched && 'is-invalid'} ${transaction.txn_from && 'is-valid'}`}
                                                        />
                                                </CCol>
                                                <CCol xs="6">
                                                    <FormRadioText 
                                                        label="To Party?" 
                                                        name="txn_to"
                                                        width="6" 
                                                        onBlur={onBlur}
                                                        onFocus={onFocus}
                                                        onKeyUp={(e) => checkInputs(e, transaction)}
                                                        value={radioItems} 
                                                        items={["My Client", "Not My Client"]}
                                                        className={`form-control ${!transaction.txn_to && touched && 'is-invalid'} ${transaction.txn_to && 'is-valid'}`}
                                                        />
                                                </CCol>
                                            </CRow>
                                        </CCol>
                                        <CCol xs="12">
                                            <FormTextArea 
                                                label="Description" 
                                                name="transaction_description"
                                                value={transaction.transaction_description} 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)}
                                                handleChange={handleChange} 
                                                placeholder="Enter ..." 
                                                className={`form-control ${!transaction.transaction_description && touched && 'is-invalid'} ${transaction.transaction_description && 'is-valid'}`}
                                            />
                                        </CCol>
                                    </CFormGroup>
                                    <FormActions 
                                        saveText="Save Overview" 
                                        handlleSaveClick={handleSaveClick} 
                                        handleCancelClick={handleCancelClick}>
                                    </FormActions>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    reportType: selectReportType(state)
});

const mapDispatchToProps = dispatch => ({
    setReportType: reportType => dispatch(SetReportType(reportType))
});

export default connect(mapStateToProps, mapDispatchToProps)(TransactionInfo);