import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class EntityLegalFormEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'A', description:'AG'},
                {value: 'G', description:'GmbH'},
                {value: 'L', description:'Ltd.'},
                {value: '-', description:'Unknown'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default EntityLegalFormEnum;