import React from 'react'
import { connect } from 'react-redux';
import { selectReportType } from '../../redux/report-type/report-type.selectors';
import FormActions from '../form-inputs/form-actions.component';
import { obj2xml } from './converter-json-xml'
import {downloadObjectAsXml, downloadXmlAsFile } from './report-downloader'

import {
    CCard,
    CCardBody,
    CRow,
    CCol
} from '@coreui/react';

const ReportGenerator = ({ name, label, reportType }) => {

    const generateXMLReport = () => {
        const report = "<report>" + obj2xml(reportType) + "</report>"; 
        downloadObjectAsXml(report, "report");
    }

    const handleCancelClick = () => {
        console.log("Hello: ", reportType);
    }

    return (
        <CCard>
            <CCardBody>
                <CRow>
                    <CCol className="col-sm-12">
                        <FormActions saveText="Generate XML Report" handleSaveClick={generateXMLReport} handleCancelClick={handleCancelClick}></FormActions>
                    </CCol>
                </CRow>
            </CCardBody>
        </CCard>
    )
}

const mapStateToProps = state => ({
    reportType: selectReportType(state)
});

export default connect(mapStateToProps, null)(ReportGenerator)