import React from 'react';
import {
    CFormGroup,
    CLabel,
    CSelect  } from '@coreui/react'

const FormDynamicSelect = ({ options, label, name, handleChange }) => (
  <CFormGroup>
      {label ? (
          <CLabel htmlFor={name}>{label}</CLabel>
          ) : null}
      {options && options.length > 0 && (
          <CSelect custom name={name} id={name} onChange={handleChange}>
              {options.map((option) => {
                  return <option value={option.value}>{option.description}</option>;
              })}
          </CSelect>
      )}
  </CFormGroup>
);

export default FormDynamicSelect;