import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class PartyTypeEnum extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            options: [
                { value: 'Person', description: 'Person' },
                { value: 'Account', description: 'Account' },
                { value: 'Entity', description: 'Entity' }
            ]
        }
    }

    render() {
        const { options } = this.state;
        const { name, label, handleChange } = this.props;

        return (
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default PartyTypeEnum;