import React from 'react';
import CombinedTable from './combined-table.component'
import usersData from '../../views/users/UsersData'

const fields = ['name','registered', 'role', 'status']

class DirectorTable extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            transactionData : usersData
        }
    }

    render(){
        const { transactionData } = this.state;
        // const { name } = this.props;
        
        return(
            <CombinedTable name="Directors Table" fields={fields} tableData={transactionData} />
        )
    }
}

export default DirectorTable;