import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class ConductionTypeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'A', description:'In-branch/Office'},
                {value: 'B', description:'ATM'},
                {value: 'C', description:'Electronic transaction'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default ConductionTypeEnum;