import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class TransactionItemTypeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {value: 'CH', description:'Chemical'},
                {value: 'C', description:'Commodity'},
                {value: 'E', description:'Equipment'},
                {value: 'F', description:'Furniture'},
                {value: 'G', description:'Goods'},
                {value: 'J', description:'Jewellery'},
                {value: 'M', description:'Machines'},
                {value: 'PM', description:'Precious Metal'},
                {value: 'P', description:'Property'},
                {value: 'S', description:'Services'},
                {value: 'V', description:'Vehicle'},
                {value: 'W', description:'Weapon'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange}/>
        )

    }
}

export default TransactionItemTypeEnum;